.container{
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    justify-content: space-between;
    z-index: 0;
    min-height: 90vh;
    padding: 100px 50px 30px 50px;
}
.imgContainer{
    position: absolute;
    height: 90vh;
    top: 0;
    z-index: -1;
    width: 80%;
    left: 20%;
}
.imgContainer img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}
.imgContainer::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background: linear-gradient(90deg, #111111 0%, transparent 70%);
    inset: 0;
}
.imgContainer::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background: linear-gradient(0deg, #111111 0%, transparent 70%);
    inset: 0;
}
.container > :nth-child(even){
    height: 48px;
}
.contentContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}